<template>
    <tab-box-m :top-menu="topMenu"></tab-box-m>
</template>

<script>
    import TabBoxM from "../../components/layout/mobile/tabBoxM";

    export default {
        name: "distributorOrder",
        components: {TabBoxM},
        data () {
            return {
                resultType:0,
                tableData:{}
            }
        },
        computed: {
            topMenu: {
                get() {
                    let topMenu = {
                        fullView: true,
                    };
                    switch (this.$route.name) {
                        case 'distributorOrderMobile':
                        case 'distributorUserOrderMobile':
                            topMenu.menuList = [
                                {
                                    text: this.$L("全部订单"),
                                    params: {
                                        type: "all"
                                    },
                                },
                                {
                                    text: this.$L("已结算"),
                                    params: {
                                        type: "settlement"
                                    },
                                },
                                {
                                    text: this.$L("未结算"),
                                    params: {
                                        type: "unSettlement"
                                    },
                                }
                            ];
                            break;
                        case 'distributorRankingMobile':
                            topMenu.menuList = [];
                            if(this.tableData.sales === 1){
                                topMenu.menuList.push({
                                    text: this.$L("销售排行榜"),
                                    params: {
                                        type: 'sales'
                                    }
                                })
                            }
                            if(this.tableData.commission === 1){
                                topMenu.menuList.push({
                                    text: this.$L("佣金排行榜"),
                                    params: {
                                        type: 'commission'
                                    }
                                })
                            }
                            if(this.tableData.offline === 1){
                                topMenu.menuList.push({
                                    text: this.$L("下级排行榜"),
                                    params: {
                                        type: 'offline'
                                    }
                                })
                            }
                            break;
                        case 'distributorSettingsMobile':
                        case 'promotionSettingsMobile':
                            topMenu.menuList = [
                                {
                                    text: this.$L("店铺设置"),
                                    router: 'distributorSettingsMobile',
                                },
                                {
                                    text: this.$L("推广海报"),
                                    router: 'promotionSettingsMobile',
                                }
                            ];
                            break;
                        case 'distributorWithdrawalMobile':
                        case 'applyWithdrawalMobile':
                            break;
                    }
                    return topMenu;
                }
            }
        },
        methods:{
            review() {
                const _self = this;
                _self.loading = true;
                _self.$api('reviewResult', {}, function (reData) {
                    if (reData.flag === 1) {
                        _self.resultType = reData.data.resultType;
                        _self.tableData = reData.data;
                        if (_self.resultType !== 1) {
                            _self.$router.push({name:'distributorAudit',query:{data:_self.tableData}});
                        }
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
        },
        created() {
            // // 先进行审核页面的判断
            this.loading = true;
            if(this.$isMobile()){
                this.review();
            }else{
                this.$router.push({name:'distributorList'});
            }
        }
    }
</script>

<style scoped>

</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>